.form-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: .5em 1em;
}

.form-group > input, .form-group > .error-message {
  width: 16em;
}

.form-group > label {
  font-weight: bold;
  display: block;
  padding-bottom: .25em;
}

input {
  font-size: 1em;
  padding:.375em .75em;
  border: 1.5px solid #ced4da;
  border-radius: .25em;
  z-index: 1;
  -webkit-transition: border-color 150ms ease-in-out, -webkit-box-shadow 150ms ease-in-out;
  transition: border-color 150ms ease-in-out, -webkit-box-shadow 150ms ease-in-out;
  -o-transition: box-shadow 150ms ease-in-out, border-color 150ms ease-in-out;
  transition: box-shadow 150ms ease-in-out, border-color 150ms ease-in-out;
  transition: box-shadow 150ms ease-in-out, border-color 150ms ease-in-out, -webkit-box-shadow 150ms ease-in-out;
}
input:hover, input:focus {
  border-color: #91979b;
}
input[type='number']:focus {
  border-color: #51ace9;
}
input::-webkit-input-placeholder {
  color: #a5a5a5;
}
input:-ms-input-placeholder {
  color: #a5a5a5;
}
input::-ms-input-placeholder {
  color: #a5a5a5;
}
input::placeholder {
  color: #a5a5a5;
}

.error-message {
  color: #f03737;
  margin-top: .25em;
  padding: .25em .845em;
  border-radius: .25em;
  background-color: #f6e2e2;
}
input + .error-message {
  margin-top: -.2em;
  padding-top: .45em;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}



